<template>
  <div class="impact9Container">
    <h2>Impact 9 - Pile ou Face</h2>

    <!-- Coin flip section -->
    <div v-if="!gameStarted && !result" class="coinContainer">
      <h2>Lancer la pièce !</h2>
      <div
        class="coin"
        :class="{ flipping: isFlipping }"
        @click="flipCoin"
      >
        <div class="face front"><img class="piece" src="@/assets/pile.png" /></div>
        <div class="face back"><img class="piece" src="@/assets/face.png" /></div>
      </div>
    </div>

    <div v-if="result && !gameStarted" class="coinResult">
      <p>{{ result }} commence en tant qu'attaquant !</p>
      <button @click="startGame" class="btn-start">Commencer</button>
    </div>

    <!-- Teams display -->
    <div v-if="gameStarted" class="teams">
      <div
        class="team"
        :class="{ active: currentTeam === 'team1' }"
      >
        <h3>Équipe 1</h3>
        <p>Points : {{ scores.team1 }}</p>
        <ul>
          <li v-for="player in teams.team1" :key="player.name">{{ player.name }}</li>
        </ul>
      </div>
      <div
        class="team"
        :class="{ active: currentTeam === 'team2' }"
      >
        <h3>Équipe 2</h3>
        <p>Points : {{ scores.team2 }}</p>
        <ul>
          <li v-for="player in teams.team2" :key="player.name">{{ player.name }}</li>
        </ul>
      </div>
    </div>

    <!-- Game started section -->
    <div v-if="gameStarted" class="gameContainer">
      <p class="gameInstructions">
        {{ gameInstruction }}
      </p>
      <!-- Balls section -->
      <div class="balls" v-if="currentRole === 'attacker'">
        <!-- White ball -->
        <div class="whiteBall">
          <img
            :src="getImagePath(0)"
            alt="Balle blanche"
            class="ball"
            @click="handleWhiteBall"
          />
          <button @click="noBallTouched" class="btn-none">Aucune boule touchée</button>
          <button @click="noBallEntered" class="btn-none">Aucune boule rentrée</button>
        </div>

        <!-- Numbered balls -->
        <div class="numberedBalls">
          <img
            v-for="n in 9"
            :key="n"
            :src="getImagePath(n)"
            :alt="`Boule ${n}`"
            class="ball"
            :class="{ 'selected': selectedBalls.includes(n) }"
            @click="toggleBallSelection(n)"
          />
        </div>

        <button 
          @click="endAttackerTurn" 
          class="btn-end-turn"
          :disabled="selectedBalls.length === 0"
        >
          Terminer le tour
        </button>
      </div>

      <!-- Defender actions -->
      <div v-else-if="currentRole === 'defender'" class="defenderActions">
        <p>Les boules sont masquées. Vous pouvez replacer les boules ou terminer votre tour.</p>
        <button @click="endDefenderTurn" class="btn-end-turn">Tour terminé</button>
      </div>

      <!-- Zone de texte pour les messages -->
      <div class="messages">
        <h3>Historique des actions</h3>
        <div v-for="(msg, index) in messages" :key="index" class="message">
          <p>{{ msg }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Impact9Game",
  data() {
    return {
      players: [],
      teams: { team1: [], team2: [] },
      isFlipping: false,
      result: "",
      gameStarted: false,
      scores: { team1: 0, team2: 0 },
      currentTeam: null,
      currentRole: "attacker",
      attemptsLeft: 3,
      whiteBallTouched: false,
      messages: [],
      previousAttacker: null,
      isError: false,
      selectedBalls: [], // Nouvelle propriété
    };
  },
  computed: {
    gameInstruction() {
      if (this.currentRole === "attacker") {
        return `Équipe ${
          this.currentTeam === "team1" ? "1" : "2"
        } attaque : Vous avez ${this.attemptsLeft} coups restants pour rentrer une boule en touchant la boule 1 en premier.`;
      } else {
        return `Équipe ${
          this.currentTeam === "team1" ? "1" : "2"
        } défend : Vous pouvez replacer les boules ou terminer votre tour.`;
      }
    },
  },
  created() {
    const savedPlayers = JSON.parse(localStorage.getItem("players")) || [];
    this.players = savedPlayers;
    this.createTeams();
  },
  methods: {
    getImagePath(n) {
      return require(`@/assets/ball_${n}.png`);
    },
    createTeams() {
      const shuffledPlayers = [...this.players].sort(() => Math.random() - 0.5);
      const mid = Math.ceil(shuffledPlayers.length / 2);
      this.teams.team1 = shuffledPlayers.slice(0, mid);
      this.teams.team2 = shuffledPlayers.slice(mid);
    },
    flipCoin() {
      if (this.isFlipping) return;

      this.isFlipping = true;
      this.result = "";
      setTimeout(() => {
        const random = Math.random() < 0.5 ? "Équipe 1" : "Équipe 2";
        this.result = random;
        this.currentTeam = random === "Équipe 1" ? "team1" : "team2";
        this.isFlipping = false;
        this.addMessage(`${random} commence en tant qu'attaquant.`);
      }, 2000);
    },
    startGame() {
      this.gameStarted = true;
      this.currentRole = "attacker";
    },
    toggleBallSelection(ballNumber) {
      const index = this.selectedBalls.indexOf(ballNumber);
      if (index === -1) {
        this.selectedBalls.push(ballNumber);
      } else {
        this.selectedBalls.splice(index, 1);
      }
    },
    endAttackerTurn() {
      if (this.selectedBalls.length > 0) {
        const points = this.selectedBalls.reduce((sum, ball) => sum + ball, 0);
        this.scores[this.currentTeam] += points;
        
        this.selectedBalls.forEach(ball => {
          this.addMessage(`L'équipe attaquante a rentré la boule ${ball}`);
        });
        this.addMessage(`L'équipe attaquante gagne ${points} points au total.`);
        
        this.selectedBalls = [];
        this.switchRole();
      }
    },
    handleWhiteBall() {
      this.addMessage("Balle blanche rentrée !");
      this.selectedBalls = [];
      this.switchRole();
    },
    noBallTouched() {
      this.addMessage("Aucune boule touchée. Les défenseurs passent attaquants.");
      this.isError = true;
      this.selectedBalls = [];
      this.switchTeam();
    },
    noBallEntered() {
      this.addMessage("Aucune boule rentrée. L'attaquant conserve son tour.");
      if (this.currentRole === "attacker") {
        this.attemptsLeft--;
        if (this.attemptsLeft === 0) {
          this.addMessage("Tentatives terminées pour les attaquants. Les défenseurs passent attaquants.");
          this.isError = true;
          this.selectedBalls = [];
          this.switchTeam();
        }
      }
    },
    endDefenderTurn() {
      this.addMessage("Les défenseurs ont terminé leur tour.");
      if (!this.isError) {
        this.restoreAttacker();
      } else {
        this.switchTeam();
      }
    },
    restoreAttacker() {
      this.addMessage(
        `L'équipe attaquante (${this.previousAttacker}) reprend son rôle.`
      );
      this.currentTeam = this.previousAttacker;
      this.currentRole = "attacker";
      this.isError = false;
    },
    switchRole() {
      this.selectedBalls = [];
      if (this.currentRole === "attacker") {
        this.currentRole = "defender";
      } else {
        this.currentRole = "attacker";
        this.attemptsLeft = 3;
      }
    },
    switchTeam() {
      this.selectedBalls = [];
      this.previousAttacker = this.currentTeam;
      this.currentTeam = this.currentTeam === "team1" ? "team2" : "team1";
      this.currentRole = "attacker";
      this.attemptsLeft = 3;
      this.isError = false;
    },
    addMessage(msg) {
      this.messages.push(msg);
    },
  },
};
</script>

<style scoped>
.impact9Container {
  font-family: "Font1";
  height: 100%;
  background-color: #1d1d1d;
  color: #f3f4f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  gap: 2rem;
}

.coinContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.coin {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(145deg, #ffffff, #d1d1d1);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  perspective: 1000px;
  transition: transform 0.5s ease;
}

.coin.flipping {
  animation: flip 2s ease infinite;
}

.face {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.front {
  transform: rotateY(0deg);
}

.back {
  transform: rotateY(180deg);
}

@keyframes flip {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(1800deg);
  }
  100% {
    transform: rotateY(3600deg);
  }
}

.piece {
  width: 120%;
}

.btn-start {
  font-family: "Font1";
  padding: 0.75rem 1.5rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn-start:hover {
  background-color: #45a049;
}

.teams {
  display: flex;
  gap: 2rem;
}

.team {
  background: #1d1d1d;
  box-shadow: 6px 6px 12px #161616, -6px -6px 12px #242424;
  padding: 1rem;
  border-radius: 10px;
  width: 200px;
}

.team.active {
  border: 2px solid #4caf50;
}

.team h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.team ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.team li {
  font-size: 1rem;
  margin: 0.5rem 0;
}

.gameContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  border-radius: 2rem;
  padding: 1%;
  background: #1d1d1d;
  box-shadow: 6px 6px 12px #161616, -6px -6px 12px #242424;
}

.balls {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.whiteBall {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.btn-none {
  font-family: "Font1";
  padding: 0.5rem 1rem;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn-none:hover {
  background-color: #d32f2f;
}

.btn-end-turn {
  font-family: "Font1";
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
}

.btn-end-turn:hover:not(:disabled) {
  background-color: #1976d2;
}

.btn-end-turn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.numberedBalls {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.ball {
  width: 80px;
  height: 80px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.ball.selected {
  transform: scale(0.9);
  opacity: 0.6;
  box-shadow: 0 0 0 3px #4caf50;
  border-radius: 50%;
}

.btn-end-turn {
  font-family: "Font1";
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
}

.btn-end-turn:hover:not(:disabled) {
  background-color: #1976d2;
}

.btn-end-turn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}
</style>